/* The styles for the navigation bar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    background-color: #222;
    color: white;
    font-size: 20px;
    padding: 0 20px;
}

.navbar a {
    color: white;
    text-decoration: none;
}

/* The styles for the main content */
.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

/* The styles for the footer */
footer {
    background-color: #222;
    color: white;
    font-size: 14px;
    padding: 20px;
    text-align: center;
}

/* The styles for the rainbow background */
body {
    background-image: linear-gradient(to right,
            red,
            orange,
            yellow,
            green,
            blue,
            indigo,
            violet);
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}
/* The styles for the form */
form {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 0 auto;
}

/* The styles for the input fields */
input[type="text"],
input[type="number"] {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
}

/* The styles for the submit button */
input[type="submit"] {
    width: 100%;
    background-color: #4caf50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

input[type="submit"]:hover {
    background-color: #45a049;
}

/* The styles for the list of investments */
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

li {
    margin: 10px 0;
}

/* The styles for the delete button */
button {
    background-color: #ff4444;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
}

button:hover {
    background-color: #cc0000;
}
